import React from 'react';
import {Container} from "react-bootstrap";
import FactImage from './assets/pubg_facts.png';
import './facts.css'

const FactsPubg = () => {
    return (
        <>
            <Container>
                <h4 className={'page-tittle'} >    PUBG Mobile...</h4>
                <div className="row row-cols-lg-2 row-cols-md-2  row-cols-1 mb-4
                ">
                    <div className="col">
                        <div className="facts">
                            <ul >
                                <li><span className={'nameFacts'}>:</span> PUBG      .  PUBG,
                                    <span className={'Brandan'}>  </span>
                                    ,   
                                    <span className={'nameTag'}> PlayerUnknown </span>
                                      DayZ: Battle Royale.</li>

                                <li><span className={'nameFacts'}> :</span>     PUBG Mobile    .</li>

                                <li><span className={'nameFacts'}>:</span>     ,    ( ).
                                              .</li>

                                <li><span className={'nameFacts'}>  :</span> PUBG Mobile        .
                                         .
                                             
                                       .</li>
                            </ul>
                        </div>
                    </div>
                    <div className="col">
                        <div className="facts-img">
                            <img src={FactImage} alt="factImage" />
                        </div>
                    </div>
                </div>

            </Container>
            <div className="line"
                 style={{backgroundColor: "silver",
                     width: "100%",
                     height: "1px",
                     marginBottom: '30px'
                 }}></div>
        </>
    );
};

export default FactsPubg;